// pages / dashboard

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Components
// import Nav from "../../../components/dashboard/nav";
import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';

// Styles
import styles from '../../../style/preloader.module.css';
// import '../../../style/theme.css';

// Action
import {getAllMessages, getMessageChat} from '../../../action/MessageAction';

class Conversation extends React.Component {
    componentDidMount() {
        this.props.getAllMessages();
    }

    navigateToMsg(msgUUID) {
        navigateTo('/dashboard/conversation/message');
        this.props.getMessageChat({
            msgUUID
        });
    }

    render() {
        let messages = this.props.messages.map((message, index) => {
            // set message type
            let messageType = "label-success";
            switch (message.type) {
                case "message":
                    messageType = "label-success";
                    break;

                case "complaint":
                    messageType = "label-danger";
                    break;

                case "suggestion":
                    messageType = "label-info";
                    break;

                case "security":
                    messageType = "label-warning";
                    break;
            }

            // return HTML
            return (
                <tr key={index} className={message.read_status ? 'read' : 'unread'}>
                    <td className="check-mail">
                        <input type="checkbox" className="i-checks" />
                    </td>
                    <td className="mail-ontact" style={{width: '33%'}}>
                        <a href="#" onClick={() => this.navigateToMsg(message.uuid)}>
                            {message.recipient.name} <small>- ({message.recipient.type})</small>
                        </a>
                        <span className={messageType + " label pull-right"}>{message.type}</span>
                    </td>
                    <td className="mail-subject">
                        <a href="#" onClick={() => this.navigateToMsg(message.uuid)}>
                            {message.subject}
                        </a>
                    </td>
                    <td className="">
                        <small>last response {moment(message.last_response_at).fromNow()}</small>
                    </td>
                    <td className="text-right mail-date">{moment(message.created_at).format('DD/MM/YYYY')}</td>
                </tr>
            );
        });


        if (this.props.user.loggedIn) {
            return (
                <div className="wrapper wrapper-content">

                    {/* Start of Helmet */}
                    <Helmet title="Conversation - communication channel" meta={[
                        {
                            name: 'description',
                            content: 'Conversation - communication channel'
                        }, {
                            name: 'keywords',
                            content: 'Conversation'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Preloader */}
                    <div className={styles.preloader1 + ' ' + (this.props.messages.length > 0 ? '' : styles.active )}>
                        <i className={styles.preloaderIcon + " fa fa-refresh"}></i>
                    </div>

                    <div className="row">
                        <ConversationSidebar messagesLength={this.props.messages.length}/>

                        <div className="col-lg-9 animated fadeInRight">
                        <div className="mail-box-header">

                            <form method="get" action="index.html" className="pull-right mail-search">
                                <div className="input-group">
                                    <input type="text" className="form-control input-sm" name="search" placeholder="Search email" />
                                    <div className="input-group-btn">
                                        <button type="submit" className="btn btn-sm btn-primary">
                                            Search
                                        </button>
                                    </div>
                                </div>
                            </form>
                            <h2>
                                Inbox ({this.props.messages.length})
                            </h2>
                            <div className="mail-tools tooltip-demo m-t-md">
                                <div className="btn-group pull-right">
                                    <button className="btn btn-white btn-sm"><i className="fa fa-arrow-left"></i></button>
                                    <button className="btn btn-white btn-sm"><i className="fa fa-arrow-right"></i></button>
                                </div>
                                <button className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="left" title="Refresh inbox"><i className="fa fa-refresh"></i> Refresh</button>
                                <button className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Mark as read"><i className="fa fa-eye"></i> </button>
                                <button className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Mark as important"><i className="fa fa-exclamation"></i> </button>
                                <button className="btn btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Move to trash"><i className="fa fa-trash-o"></i> </button>
                            </div>
                        </div>
                            <div className="mail-box">
                            <table className="table table-hover table-mail">
                                <tbody>
                                    {messages}
                                </tbody>
                            </table>
                            </div>
                        </div>
                    </div>
                </div>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user, messages: state.messages.messages};
}

const mapDispatchToProps = (dispatch) => {
    return {getAllMessages, getMessageChat};
}

export default connect(mapStateToProps, mapDispatchToProps())(Conversation);



